import http from '../http-common';

const mapping = 'auth';

class AuthService {

    /**
     * Crea una nueva cuenta de usuario
     * @param {Object} nuevoUsuario Informacin del nuevo usuario
     * @returns Mensaje de confirmacin
     */
    signUp(nuevoUsuario){
        return http
            .post(`${mapping}/signUp`, nuevoUsuario);
    }

    /**
     * Permite obtener una nueva autorizacin para un nuevo inicio de sesin
     * @param {Object} usuario 
     * @returns Token de autorizacin
     */
    async signIn(usuario){
        const res = await http
            .post(`${mapping}/signIn`, usuario);
        if (res.data.token) {
            localStorage.setItem("user", res.data.token);
        }
        return res.data;
    }

    /**
     * Permite cambiar la contrasea del usuario actual
     * @param {Object} usuario Datos del usuario, nueva clave y confirmacin de nueva clave
     * @returns Mensaje de confirmacin de cambio de clave
     */
    chgpwd(usuario){
        return http
            .put(`${mapping}/chgpwd`, usuario);
    }

    /**
     * Cierra la sesin actual
     */
    logout(){
        localStorage.removeItem("user");
    }

    /**
     * Obtiene el token de autorizacin para cada peticin
     * @returns La autorizacin actual del usuario en sesin
     */
    getUsuarioActual(){
        return JSON.parse(localStorage.getItem("user"));
    }
}

export default new AuthService();